import React , { useState} from "react";
import { Link, useNavigate } from "react-router-dom";
import { useAuth } from "./AuthProvider";
import axios from "axios";
import SearchResults from "./SearchResult";


interface HeaderPs {

  onSearch: (query: string) => void
}

const Header: React.FC = () => {
  const { authToken, username, logout,apiUrl } = useAuth();
  const [isDropdownOpen, setIsDropdownOpen] = useState(false);
  const [searchQuery, setSearchQuery] = useState("");
  const [searchResults, setSearchResults] = useState<any[]>([]);
  
  const navigate = useNavigate();

// Toggle dropdown menu
const toggleDropdown = () => {
  setIsDropdownOpen((prev) => !prev);
};


const handleSearchChange = async (event: React.ChangeEvent<HTMLInputElement>) => {
  const query = event.target.value;
  setSearchQuery(query);

  if (query.trim() === "") {
    setSearchResults([]); // Clear results if the search is empty
    return;
  }

  try {
    // Send the search request to the backend API
    const response = await axios.get(`${apiUrl}/search`, { params: { query } });
    setSearchResults(response.data);  // Set the search results
  } catch (error) {
    console.error("Error fetching search results:", error);
  }
  
};

const handleSearchSubmit = (event: React.FormEvent) => {
  event.preventDefault();
  if (searchQuery.trim() !== "") {
    
    // Perform search action here, e.g., navigate to search results page
    console.log("Searching for:", searchQuery);
  }

};

const handleLogout = () => {
  logout();
  setIsDropdownOpen(false); // Close dropdown
};

  // Handle user selection
  const handleUserSelect = (user) => {
    // Navigate to the user's profile page using their username
    navigate(`/user/${user.username}`);
    setSearchQuery("");
  setSearchResults([]);
  };

return (
  <header className="bg-gray-800 text-white shadow-md">
    <div className="container mx-auto flex justify-between items-center py-4 px-6">
      {/* Company Name */}
      <div className="text-2xl font-bold">
        <span className="cursor-pointer">Plog</span>
      </div>

      {authToken && (
       <SearchResults
       searchQuery={searchQuery}
       searchResults={searchResults}
       handleSearchChange={handleSearchChange}
       handleUserSelect={handleUserSelect}
       handleSearchSubmit={handleSearchSubmit}
     />
      )}

      {/* Login Button or Profile Dropdown */}
      <div className="relative">
        {authToken ? (
          <button
            onClick={toggleDropdown}
            className="flex flex-col items-center gap-1 bg-gray-700 hover:bg-gray-600 text-white font-medium py-1 px-4 rounded-lg transition duration-200"
          >
            <img 
              src="data:image/png;base64," // Replace with actual profile image URL
              alt="Profile"
              className="w-8 h-8 rounded-full" 
            />
            <div className="flex items-center gap-1">
              <span>Me</span>
              
              <svg
                className="text-sm"
                xmlns="http://www.w3.org/2000/svg"
                viewBox="0 0 320 512"
                fill="currentColor"
                width="16"
                height="16"
              >
                <path d="M160 384c-8.2 0-16.4-3.1-22.6-9.4L9.4 246.6C-3.1 234.1-3.1 213.9 9.4 201.4s32.8-12.5 45.3 0L160 306.7l105.4-105.4c12.5-12.5 32.8-12.5 45.3 0s12.5 32.8 0 45.3L182.6 374.6c-6.2 6.3-14.4 9.4-22.6 9.4z" />
              </svg>
            </div>
          </button>
        ) : (
          <Link
            to="/login"
            className="bg-blue-500 hover:bg-blue-600 text-white font-medium py-2 px-4 rounded-lg transition duration-200"
          >
            Log In
          </Link>
        )}

        {/* Dropdown Menu */}
        {isDropdownOpen && authToken && (
          <div className="absolute right-0 mt-2 w-48 bg-white text-black shadow-lg rounded-lg">
            <Link
              to={`/user/${username}`}
              className="block px-4 py-2 hover:bg-gray-200"
              onClick={() => setIsDropdownOpen(false)}
            >
              Go to Profile
            </Link>
            <button
              onClick={handleLogout}
              className="w-full text-left px-4 py-2 hover:bg-gray-200"
            >
              Log Out
            </button>
          </div>
        )}
      </div>
    </div>
  </header>
);
};

export default Header;